body {
  margin: 0;
  color: white;
  background: rgb(14, 9, 26)
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}
input{
  width: 300px;
  height: 50px;
  font-size: 36px;
  border-radius: 6px;
}
button{
  margin-left: 10px;
  padding: 14px;
  border-radius: 10px;
  font-size: 24px;
  background-color: #28243d;
  color: #ffd000d0;
  cursor: pointer;
}
.form_container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 40px;
}
.result, .heroe{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 200px;
  height: 60px;
  border-radius: 20px;
  font-size: 36px;
}
.heroe{
  background-color: rgb(8, 78, 8);
  margin-top: 6px;
  height: 120px;
}
h1{
  font-size: 60px;
  color: #ffd000d0;
  margin-top: 50px;
}
.result {
  background-color: rgb(109, 13, 13);
  margin-top: -14px;
}
.result_container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.hero_input{
  margin-left: 10px;
  color: #ffd000;;
  background-color: #adadad3f;
}
.custom-canvas {
  width: 600px;
  height: 600px;
  position: relative;
  z-index: -2;
  border-radius: 20px;
}
.box{
  margin-top: 140px;
  box-shadow: inset 0px 0px 10px 8px rgba(0,0,0,0.9);
  width: 600px;
  height: 600px;
}
.heroe_name{
  text-align: center;
  font-size: 38px;
  color: #ffd000d0;
  letter-spacing: 2px;
}

@media only screen and (min-height:1200px) {
  .box{
    margin-top: 50px;
    width: 900px;
    height: 900px;
  }
  .custom-canvas{
    width: 900px;
    height: 900px;
  }
  ul li{
    font-size: 3.6em;
  }
}